import React, { Component } from 'react';
import { withFirebase } from '../Firebase';
import { connect } from 'react-redux';
import { compose } from 'recompose';

import 'emoji-mart/css/emoji-mart.css';
import { Picker } from 'emoji-mart';

class ChatBox extends Component {
  constructor(props) {
    super(props);

    this.state = {
      message: '',
      authUser: ''
    };

    this.setMessage = this.setMessage.bind(this);
  }

  setMessage = e => {
    e.preventDefault();

    this.setState({
      message: e.target.value
    });
  };

  componentDidMount() {}

  static getDerivedStateFromProps(props, state) {
    if (props.authUser !== state.authUser) {
      return {
        authUser: props.authUser
      };
    }

    // Return null if the state hasn't changed
    return null;
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props.authUser !== prevState.authUser) {
      this.setState({
        authUser: this.props.authUser
      });
    }
  }

  submitMessage = e => {
    e.preventDefault();
    let id = this.props.id;
    let month = parseInt(this.props.month);
    let day = parseInt(this.props.day);
    let title = this.props.title;
    let message = this.state.message;
    let date = new Date();
    var messageMonth = date.getMonth();
    this.props.getMessage(id, month, day, title, message);

    this.setState({
      message: ''
    });
  };

  addEmoji = e => {
    if (e.unified.length <= 5) {
      let emojiPic = String.fromCodePoint(`0x${e.unified}`);
      this.setState({
        message: this.state.message + emojiPic
      });
    } else {
      let sym = e.unified.split('-');
      let codesArray = [];
      sym.forEach(el => codesArray.push('0x' + el));

      let emojiPic = String.fromCodePoint(...codesArray);
      this.setState({
        message: this.state.message + emojiPic
      });
    }
  };

  render() {
    return (
      <div>
        <form onSubmit={this.submitMessage}>
          <textarea onChange={this.setMessage} value={this.state.message} />
          <button onClick={this.submitMessage}>Submit</button>
        </form>
        <span>
          <Picker onSelect={this.addEmoji} />
        </span>
      </div>
    );
  }
}


const mapDispatchToProps = dispatch => ({
    onSetMessage: message => dispatch({ type: 'MESSAGE_SENT', message }),
});


export default compose(
    withFirebase,
    connect(
        null,
        mapDispatchToProps
    )
)(ChatBox)